
.login{
    display: flex;
    flex-direction: column;
    align-items: center;
    height:100vh;
    background-color: white;
}



.login__image{
    object-fit: contain;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: auto;
    width: 100px;
}


.login__container{
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border:1px solid lightgray;
    padding: 20px;
    
}

.login__container > form > input{
    margin-bottom: 10px;
    width: 98%;
    background-color: white;
    height: 30px;
    border-radius: 3px;
}

.login__container > p{
    margin-top: 15px;
    font-size: 12px;
}


.login__container > h1{
    font-weight: 500;
    margin-bottom: 20px;
}


.login__container > form > h5{
    margin-bottom: 5px;
}


.login__signInButton{
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29 ; 
}


.login__registerButton{
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid darkgray;
    margin-top: 10px;
}